const startBtn = document.querySelector("#keys-start");
const resetBtn = document.querySelector("#keys-reset");
const keysOutput = document.querySelector("#keys-output");
const keys = [
  {
    name: "KeyA",
    letter: "a",
  },
  {
    name: "KeyR",
    letter: "r",
  },
  {
    name: "KeyF",
    letter: "f",
  },
  {
    name: "KeyC",
    letter: "c",
  },
  {
    name: "KeyL",
    letter: "l",
  },
  {
    name: "KeyS",
    letter: "s",
  },
  {
    name: "KeyM",
    letter: "m",
  },
  {
    name: "KeyP",
    letter: "p",
  },
  {
    name: "KeyX",
    letter: "x",
  },
  {
    name: "KeyH",
    letter: "h",
  },
];
let currentIndex = 0;

function setCurrentKey() {
  if (currentIndex >= keys.length) {
    currentIndex = 0;
  }
  keysOutput.textContent = keys[currentIndex].letter;
  currentIndex += 1;
}

function startGame() {}

window.addEventListener("keydown", (e) => {
  keysOutput.textContent = keys[currentIndex].letter;
});
